.util {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 24px;
    background-color: var(--main-bg);

    .util_left {
        width: 32px;
        height: 32px;
        position: relative;

        &:hover {
            .util_theme {
                display: block;
            }

            .util_left-tooltip {
                display: block;
            }
        }

        img {
            width: 100%;
            height: 100%;
            cursor: pointer;
            opacity: 0.9;
            transition: all 0.3s linear;

            &:hover {
                opacity: 1;
            }
        }

        .util_theme {
            display: none;
            position: absolute;
            bottom: calc(100% + 8px);
            left: 0%;
            z-index: 999;
            width: fit-content;
        }

        &::before {
            content: '';
            position: absolute;
            top: -8px;
            left: 0;
            width: 120%;
            height: 8px;
            background-color: transparent;

        }

        .util_left-tooltip {
            display: none;
            position: absolute;
            bottom: calc(100% + 8px);
            left: 0%;
            padding: 8px;
            font-size: 12px;
            font-weight: normal;
            color: var(--text-white);
            background-color: var(--text-color-faint);
            border-radius: 4px;
            white-space: nowrap;
            width: fit-content;
            z-index: 999;
        }
    }

    .util_center {
        display: flex;
        align-items: center;

        .util_center-item {
            margin-left: 8px;
        }
    }

    .util_right {

        .util_right-btn {
            padding: 10px 16px;
            font-size: 14px;
            box-shadow: var(--second-box-shadow);
            color: var(--text-white);
            background-color: var(--main-color);
            transition: all 0.3s linear;
            border-radius: 4px;
            cursor: pointer;
            font-weight: 500;
            text-transform: uppercase;
            white-space: nowrap;
            letter-spacing: 0.75px;

            &:hover {
                background-color: var(--main-color-bold);
                box-shadow: var(--third-box-shadow);
            }
        }
    }
}

@media (max-width: 1024px) {  
    .util {

        .util_center {
            display: none;
        }
    }
}