.button-related {
    width: fit-content;
    min-width: fit-content;
    min-height: 32px;
    padding: 7px 12px;
    border-radius: 24px;
    background-color: #f0f0f1;
    color: #4a4b57;
    font-size: 14px;
    white-space: nowrap;
    margin: 0 2px;

    &:hover {
        cursor: pointer;
        box-shadow: var(--second-shadow-theme);

    }

    &.active {
        color: var(--text-white);
        background-color: #5a5b6a;

        &:hover {
            cursor: default;
            box-shadow: none;
        }
    }
}