.sort-area_title {
    width: 100%;
    margin-bottom: 24px;

    .sort-area_title-big {
        display: flex;
        align-items: center;
        height: 36px;

        img {
            display: none;
            width: 24px;
            height: 24px;
            margin-right: 8px;
        }

        span {
            color: var(--text-color);
            font-size: 24px;
        }
    }

    .sort-area_title-sub {
        color: var(--text-color-light);
        font-size: 13px;
        letter-spacing: 0.44px;
    }

    &.image {
        .sort-area_title-big {
    
            img {
                display: block !important;
            }
        }
    }
}