.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--main-color);
    height: auto;
    min-height: var(--height-header);
    padding: 0 24px;
    color: var(--text-white);
    box-shadow: var(--box-shadow);

    .header_logo {
        font-family: 'Abril Fatface', cursive;
        font-size: 24px;
        line-height: 32px;
        display: flex;
        align-items: center;
        justify-content: center;

        .header_logo-icon {
            width: 40px;
            height: 40px;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: var(--second-color);
            border-radius: 6px;
            color: var(--main-color);
            margin-right: 24px;
            font-family: "Abril Fatface", cursive;
        }

        .header_logo-name {
            font-family: "Abril Fatface", cursive;
            margin-right: 24px;
        }
    }

    .header_navbar {
        
        display: flex;
        align-items: center;
        justify-content: center;    

        .header_navbar-item {
            font-size: 20px;
            font-style: normal;
            font-weight: 500;
            letter-spacing: .15px;
            line-height: 28px;
            text-decoration: none;
            text-align: center;
            color: var(--third-color);
            min-height: 64px;
            min-width: 96px;
            margin-right: 8px;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: .21s ease-in-out;
            

            &:hover {
                color: var(--text-white);
            }

            &.active {
                cursor: default;
                color: var(--text-white);
                position: relative;
            }

            &.active::after {
                content: "";
                position: absolute;
                width: 100%;
                height: 4px;
                border-radius: 4px 4px 0 0;
                left: 50%;
                transform: translateX(-50%);
                bottom: -1px;
                background-color: var(--main-bg);
                z-index: 1;
                box-shadow: var(--box-shadow);
                // animation: growl 0.3s linear;
                animation: growl 0.35s cubic-bezier(0.35, 0, 0.25, 1);
                
            }

            @keyframes growl {
                0% {
                    width: 0;
                    opacity: 0;
                }; 
                100% {
                    width: 100%;
                    opacity: 1;
                }
            }
        }

    }

    .header_utils {
        display: flex;
        align-items: center;
        justify-content: center;

        .header_utils-search {
            height: 40px;
            margin-right: 8px;
        }

        .header_utils-cart {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 40px;
            height: 40px;
            font-size: 24px;
            margin-right: 8px;
            color: var(--text-white);
            border-radius: 50%;
            background-color: var(--main-color-bold);
            cursor: pointer;

            span {
                position: absolute;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 50%;
                background-color: red;
                line-height: normal;
                color: var(--text-white);
                width: 22px;
                height: 22px;
                font-size: 18px;
                top: -3px;
                right: -3px;
            }
        }
        
        .header_utils-avatar {

            img {
                width: 40px;
                height: 40px;
                object-fit: cover;
                border-radius: 50%;
                cursor: pointer;

            }
        }
    }
}

@media (max-width: 1440px) {
    .header {
        min-height: var(--height-header-small);

        .header_navbar {
            padding: 0 12px;
    
            .header_navbar-item {
                font-size: 14px;
                letter-spacing: .1px;
                min-height: var(--height-header-small);
                min-width: 64px;
                margin-right: 20px;
                
                &.active::after {
                    height: 1.4px;
                    border-radius: 0;
                    bottom: 0px;
                }
            }
    
        }
    
        .header_utils {
            .header_utils-search {
                height: 32px;
            }
    
            .header_utils-cart {
                min-width: 32px;
                width: 32px;
                height: 32px;
                font-size: 22px;

                span {
                    width: 19px;
                    height: 19px;
                    font-size: 16px;
                    top: -5px;
                    right: -5px;
                }
            }
            
            .header_utils-avatar {
    
                img {
                    width: 32px;
                    height: 32px;
    
                }
            }
        }
    }
}

@media (max-width: 1024px) { 
    .header {
        position: relative;
        font-size: 20px;
        padding: 10px 12px var(--height-header-small);
        height: var(--height-header-mobile);

        .header_logo {
    
            .header_logo-icon {
                font-family: 'Abril Fatface', cursive;
                width: 32px;
                height: 32px;
                margin-right: 8px;
            }
    
            .header_logo-name {
                font-family: 'Abril Fatface', cursive;
                font-size: 20px;
                margin-right: 12px;
            }
        }

        .header_navbar {
            position: absolute;
            left: 0;
            height: var(--height-header-small);
            bottom: 0;
            width: 100%;
            background-color: var(--main-color);
            padding: 0 12px;

            .header_navbar-item {
                font-size: 14px;
                letter-spacing: .1px;
                min-height: var(--height-header-small);
                min-width: 64px;
                margin: 0 10px;
            }
        }

        .header_utils {
            width: 60%;
            justify-content: flex-end;

            .header_utils-search {
                flex: 1;
                margin-right: 12px;
            }
    
            .header_utils-cart {
                margin-right: 12px;
            }
        }
    }
}

@media (max-width: 647px) { 
    .header {
        .header_navbar {
            .header_navbar-item {
                margin: 0 6px;
            }
        }
    }
    
}