.overlay {
    display: none;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 1000;
    opacity: 0;
    background-image: var(--overlay-color);
    transition: opacity .4s cubic-bezier(.25,.8,.25,1);

    &.over {
        display: block;
        opacity: 1;
    }

    &.absolute {
        z-index: 3;
        position: absolute;

    }
}
