.button-filter {
    padding: 8px 12px;
    width: fit-content;
    height: fit-content;
    display: flex;
    align-items: center;
    border-radius: 4px;
    cursor: pointer;

    span {
        color: var(--main-color);
        text-transform: uppercase;
        white-space: nowrap;
        letter-spacing: 0.75px;
        font-size: 14px;
        font-weight: 500;
        margin-right: 10px;
    }

    svg {
        fill: var(--main-color);
        width: 24px;
        height: 24px;
    }

    &:hover {
        box-shadow: var(--second-box-shadow);
    }
}