.category {
    width: 248px;
    min-height: 56px;
    height: 56px;
    min-width: 248px;
    display: flex;
    align-items: center;
    word-wrap: break-word;

    .category_left {
        margin: 0 4px; 
        width: 40px;
        height: 40px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        

        .cateory_icon {
            opacity: 0;
            color: var(--main-color);
            font-size: 24px;
        }
    }

    .category_right {
        flex: 1;

        .category_title {
            font-size: 16px;
            font-weight: normal;
            color: var(--text-color);
            line-height: 22px;
        }
        
        .category_subtitle {
            font-size: 14px;
            font-weight: normal;
            color: var(--text-color-light);
            margin-top: 3px;
        }
    }

    &:hover {
        cursor: pointer;

       
    }

    &.parent {
        .cateory_icon {
            opacity: 1;
        }

        &:hover {
            .category_left {
                box-shadow: var(--second-box-shadow);
            }
        }

        &.open {

            .cateory_icon {
                color: var(--text-color);
            }
    
            .category_title {
                font-weight: bold;
                color: var(--text-color);
            }
    
            
            &:hover {
                .category_left {
                    box-shadow: none;
                }
    
                .cateory_icon {
                    color:  var(--main-color);
                }
            }
        }
    }

}