.button-inline {
    display: flex;
    align-items: center;
    width: fit-content;
    min-width: fit-content;
    background-color: var(--faint-color);
    height: 32px;
    // opacity: 0.9;
    border-radius: 4px;
    position: relative;

    .button-inline_active {
        position: absolute;
        height: 100%;
        left: 0;
        background-color: var(--main-bg);
        border-radius: 4px;
        box-shadow: var(--box-shadow);
        z-index: 1;
        transition: .21s ease-in-out;
    }

    .button-inline_item {
        z-index: 2;
        font-size: 14px;
        font-weight: 500;
        white-space: nowrap;
        color: var(--text-color-faint);
        padding: 0 22px;
        height: 100%;
        width: fit-content;
        min-width: fit-content;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 4px;
        transition: .21s ease-in-out;
        user-select: none;

        svg {
            width: 22px;
            height: 22px;
            border-radius: 24px;
            fill: var(--text-color-faint);
        }
        

        &:hover {
            color:  var(--main-color);
            cursor: pointer;

            svg {
                fill:  var(--main-color);
            }
        }

        &.active { 
            color:  var(--main-color);

            svg {
                fill:  var(--main-color);
            }

            &:hover {
                cursor: default;
            }
        }
    }
}