.checkbox {
    width: 100%;
    height: 40px;
    min-height: 40px;
    min-width: 272px;
    padding: 6px 12px;
    border-radius: 4px;
    margin-bottom: 4px;
    cursor: pointer;

    display: flex;
    align-items: center;
    transition: all 0.3s linear;

    .checkbox_box {
        width: 34px;
        height: 34px;
        border-radius: 50%;
        margin-right: 12px;
        display: flex;
        align-items: center;
        justify-content: center;

        .checkbox_box-square {
            width: 18px;
            height: 18px;
            border: 2px solid var(--main-color);
            border-radius: 3px;

            display: flex;
            align-items: center;
            justify-content: center;

            svg {
                display: none;
                fill:  var(--text-color);
            }
        }

        
    }

    .checkbox_content {
        font-size: 14px;
        font-weight: 400;
        color: var(--text-color);
    }

    &:hover {
        background: var(--main-bg);
        box-shadow: var(--second-box-shadow);

        .checkbox_box {
            background-color: #ebf2ff;
        }
    }

    &.active {

        background-color: #d8e6ff;

        .checkbox_box { 

            .checkbox_box-square {
                background-color: var(--main-color);

                svg {
                    display: block;
                    fill: var(--text-white);
                }
            }
        }

        &:hover {
            background-color: #d8e6ff;
            box-shadow: none;

            .checkbox_box {
                background: none;
            }
        }
    }
    
}