.filter {
    padding-left: 12px;
    margin-bottom: 24px;

    .filter_button-category {

        margin-bottom: 40px;
    }

    .filter_filter {
        margin-top: 40px;
    }
}

@media (max-width: 1024px) {  
    .filter {
        margin-top: 12px;

        .filter_button-category {
            display: none;
        }
    }
}

@media (max-width: 640px) { 
    .filter_filter {
        display: none !important;
    }
}