.layout {
    position: relative;
    background-color: var(--main-bg);

    .layout_header {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        z-index: 10;
    }

    .layout_filter-mobile {
        .layout_filter {
            display: none;
        }
    }

    .layout_filter {
        position: fixed;
        top: var(--height-header);
        bottom: var(--height-utils);
        width: var(--width-filter);
        overflow: auto;
        padding-top: 4px;
        padding-left: 8px;
        padding-right: 8px;
        margin-top: 24px;
        margin-right: 5px;
        z-index: 9;
        background-color: var(--main-bg);

        // &::-webkit-scrollbar {
        //     opacity: 0;
        // }
    }

    .layout_sort-mobile {
        display: block;
        position: fixed;
        top: calc(var(--height-header-mobile));
        right: calc(0px - var(--width-filter));
        bottom: var(--height-utils);
        width: var(--width-filter);
        overflow: auto;
        padding-top: 4px;
        padding-left: 8px;
        padding-right: 8px;
        z-index: 9;  
        background-color: var(--main-bg);
        transition: right .3s linear;

        &::-webkit-scrollbar {
            width: 0!important;
        }

        &.open {
            right: 0;
            box-shadow: var(--shadow-open);
            
        }
    }

    .layout-main {
        z-index: 0;
        width: calc(100% - var(--width-filter));
        margin-left: var(--width-filter);
        margin-top: calc(var(--height-header));
        padding-top: 24px;
        padding-bottom: 48px;

        .layout-main_sortby-mobile {
            display: none;
            align-items: center;
            justify-content: space-between;
            margin: 12px 16px;
            height: 40px;

            .layout-main_sortby-mobile--logo {
                display: flex;
                align-items: center;

                img {
                    width: 24px;
                    height: 24px;
                    object-fit: contain;
                    margin-right: 12px;
                }

                .layout-main_sortby-mobile--logo-text {
                    font-size: 24px;
                    font-weight: 500;
                    letter-spacing: 0.75px;
                }
            }

            .layout-main_sortby-mobile--button {
                padding: 0 12px;
                border-radius: 4px;
                width: fit-content;
                height: 100%;
                display: flex;
                align-items: center;
                cursor: pointer;
                transition: all 0.2s linear;
                border: 1px solid var(--faint-color);
                background-color: var(--faint-color);
            
                &:hover {
                    box-shadow: var(--second-box-shadow)
                }
            
                svg {
                    width: 24px;
                    height: 24px;
                    fill: var(--main-color);
                    margin-right: 12px;
                }
            
                span {
                    color: var(--main-color);
                    font-weight: 400;
                    font-size: 14px;
                    letter-spacing: 0.47px;
                }
            }
        }

        .layout-main_content {
            width: 100%;
            margin-bottom: 40px;
            

            .layout-main_content-wrap {
                height: 100%;
                margin: 0 16px;
                margin-left: 12px;
                border-radius: 8px;
                padding: 16px;
                box-shadow: var(--shadow-theme);

                .layout_sort {
                    // width: 100%;
                }

                .layout_action {
                    margin-bottom: 24px;
                   
                }

                .layout_related {
                    

                }
            }
        }

        .layout-main_propmotion {
            width: 100%;
            padding: 0 16px 24px 12px;
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            gap: 12px;
            
        }
    }
    
    .layout_button-mobile {
        display: none;
    }

    .layout_categories {
        position: fixed;
        top: 20%;
        left: 12%;
        right: 12%;
        height: 80%;
        padding: 24px;
        background-color: var(--main-bg);
        box-shadow: var(--shadow-open);
        color: var(--text-color-bold);
        border-top-left-radius: 24px;
        border-top-right-radius: 24px;
        z-index: 1001;
        transform: translateY(100%);
        transition: all 0.3s ease;

        &.appearance {
            transform: translateY(0%);
        }

    }

    .layout_util {
        position: fixed;
        // background-color: #999;
        bottom: 0;
        left: 0;
        right: 0;
        height: var(--height-utils);
        z-index: 10;
        box-shadow: var(--shadow-theme);
        z-index: 10;
    }
}

@media (max-width: 1440px) { 
    .layout {

        .layout_filter {
            top: var(--height-header-small);

        }
    
        .layout-main {
            margin-top: calc(var(--height-header-small));
    
            
        }
    }
}

@media (max-width: 1248px) { 
   
}


@media (max-width: 1112px) { 
    .layout {
    
        .layout_categories {
            left: 8%;
            right: 8%;
        }
    }
}

@media (max-width: 1024px) { 
    .layout {

        .layout_filter-mobile {
            .layout_filter {
                margin-top: 0;
                display: block;

                &::-webkit-scrollbar {
                    width: 0!important;
                }
            }
        }
    
        .layout_filter {
            top: calc(var(--height-header-mobile));
            left: calc(0px - var(--width-filter));
            transition: left .3s linear;
            display: none;
            
            &.open {
                left: 0;
                margin-top: 0;
                background-color: var(--main-bg);
                box-shadow: var(--shadow-open);
                
            }
        }

        .layout_sort-mobile {
           
        }

        .layout-main {
            margin-top: calc(var(--height-header-mobile));
            margin-left: 0;
            padding-bottom: 112px;
            width: 100%;
            
            .layout-main_content {
                .layout-main_content-wrap {
                    .layout_related {
                        // flex-wrap: nowrap;
                    }
                }
            }
        }

        .layout_button-mobile {
            position: fixed;
            left: 50%;
            bottom: calc(var(--height-utils) + 18px);
            width: fit-content;
            transform: translateX(-50%);
            display: flex;
            align-items: center;
            gap: 24px;
            z-index: 1;

            .layout_button-mobile-filter {
                width: 56px;
                height: 56px;
                border-radius: 50%;
                background-color: var(--main-bg);
                box-shadow: var(--second-box-shadow);
                cursor: pointer;
                display: flex;
                align-items: center;
                justify-content: center;
                transition: all 0.3s ease;

                .layout_button-mobile-filter-icon {
                    width: 24px;
                    height: 24px;
                    fill: var(--main-color);
                }

                &:hover {
                    box-shadow: var(--third-box-shadow);
                }
            }
        }
    }
}

@media (max-width: 640px) { 

    .layout-main_sortby-mobile {
        display: flex !important;
    }

    .layout_sort {
        display: none !important;
    }

    .layout_action {
        display: none !important;
    }

    .layout_related {
        display: none !important;
    }
}


@media (min-width: 499px) and (max-width: 548px) { 
    .layout {

        .layout-main_sortby-mobile {
            margin: 12px 42px !important;
        }
    
        .layout-main_content-wrap {
            margin: 0 42px !important;
        }

        .layout-main_propmotion {
            padding-left: 42px !important;
            padding-right: 42px !important;
            grid-template-columns: 1fr !important;
            
        }
    }
}

@media (min-width: 409px) and (max-width: 498px) { 
    .layout {

        .layout-main_sortby-mobile {
            margin: 12px 32px !important;
        }
    
    
        .layout-main_content-wrap {
            margin: 0 32px !important;
        }

        .layout-main_propmotion {
            padding-left: 32px !important;
            padding-right: 32px !important;
            grid-template-columns: 1fr !important;
            
        }
    }
}

@media (max-width: 474px) { 
    .layout {

        .layout-main_sortby-mobile {
            margin: 12px 16px !important;
        }

        .layout-main_content-wrap {
            margin: 0 16px !important;
        }

        .layout-main_propmotion {
            padding-left: 16px !important;
            padding-right: 16px !important;
            grid-template-columns: 1fr !important;
            
        }

    
        .layout_categories {
            padding: 24px 12px;
        }

       
    }
}