.collapse {
    user-select: none;
    max-width: 287px;
    width: 100%;
    margin-bottom: 24px;
    border-radius: 4px;
    box-shadow: var(--shadow-theme);
    overflow: hidden;

    .collapse_header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 24px;
        height: 48px;
        width: 100%;
        cursor: pointer;
        position: relative;
        transition: all 0.2s linear;

        .collapse_header-title {
            font-size: 16px;
            color: var(--text-color);
            font-weight: 400;


            margin-right: 16px;
        }

        .collapse_header-icon {
           position: absolute;
           top: 12px;
           right: 24px;


            svg {
                transition: all 0.3s linear;
                width: 24px;
                height: 24px;
                fill: var(--text-color);
            }
        }
    }

    .collapse_body {
        max-height: 0;
        padding: 0 8px;
        transition: max-height 0.3s linear;

    }

    &:hover {
        background: var(--hover-color);
    }

    &.open {

        background-color: var(--faint-bg);

        .collapse_header {
            height: 64px;

            .collapse_header-icon {

                svg {
                    transform: rotate(180deg);
                }
            }

            &:hover {
                background: var(--hover-color);
            }
        }

        .collapse_body {
            margin-bottom: 8px;
        }
    }
}

@keyframes colapse {
    0% {
        height: 0%;
    };
    100% {
        height: 100%;
    }
}