:root {
    --main-bg: #ffffff;
    --second-bg: #fafafb;
    --faint-bg: #f0f0f17a;

    --combobox-bg: linear-gradient(90deg,#5a5b6a,#9696a0);
    
    --text-color-bold: #000000de;
    --text-color: #19191d;;
    --text-color-light: #787885;
    --text-color-faint: #9696a0;
    --text-white: #fff;

    --hover-color: #0000000a;

    --border-color: #b4b4bb;

    --overlay-color: linear-gradient(0deg,#1b4ea329,#1b4ea329);

    --shadow-theme-big: 0 4px 8px #00000033, 0 8px 16px #00000033;
    --shadow-theme: 0 1px 2px #b4b4bb, 0 2px 4px #d2d2d6;
    --second-shadow-theme: 0 2px 4px #b4b4bb, 0 4px 8px #d2d2d6;
    --shadow-open: 0 8px 10px -5px rgb(0 0 0 / 20%), 0 16px 24px 2px rgb(0 0 0 / 14%), 0 6px 30px 5px rgb(0 0 0 / 12%);

    --main-color-bold: #1b4ea3;
    --main-color:  #2264d1;
    --second-color: #9dc2ff;
    --third-color: #c4daff;
    --faint-color: #ebf2ff;
    --box-shadow: 0 1px 2px var(--second-color),0 2px 4px var(--third-color);
    --second-box-shadow: 0 2px 4px var(--second-color),0 4px 8px var(--third-color);
    --third-box-shadow: 0 4px 8px var(--second-color),0 8px 16px var(--third-color);


    --main-color-bold-blue: #1b4ea3;
    --main-color-blue:  #2264d1;
    --second-color-blue: #9dc2ff;
    --third-color-blue: #c4daff;
    --faint-color-blue: #ebf2ff;

    --main-color-bold-red: #cb080f;
    --main-color-red:  #dc2026;
    --second-color-red: #ff4a6b;
    --third-color-red: #f36065;
    --faint-color-red: #fed7d8;

    --main-color-bold-cyan: #03afac;
    --main-color-cyan:  #0ba19e;
    --second-color-cyan: #10d4d2;
    --third-color-cyan: #5cd4d2;
    --faint-color-cyan: #dff3f2;

    --main-color-bold-green: #008004;
    --main-color-green:  #1f8122;
    --second-color-green: #4caf50;
    --third-color-green: #5aac5d;
    --faint-color-green: #cdf3ce;

    --main-color-bold-orange: #bb7101;
    --main-color-orange:  #d68102;
    --second-color-orange: #fca11a;
    --third-color-orange: #f5b452;
    --faint-color-orange: #ffe6c1;


    --transition-cubic: cubic-bezier(0.175, 0.885, 0.32, 1.275);

    /* Header */
    --height-header: 64px;
    --height-header-small: 48px;
    --height-header-mobile: 100px;
    /* Filter */
    --width-filter: 320px;

    /* Utils */
    --height-utils: 48px;

}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
    font-weight: normal;
    font-family: 'Roboto', sans-serif;
}

/* width */
::-webkit-scrollbar {
    width: 5px;
    height: 5px;
    position: absolute;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #9dc2ff;
    border-radius: 4px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #78a4ed;
    cursor: pointer;
}