.sort-area {
    padding: 12px;

   

    .sort-area_filter {

        .sort-area_filter-button {
            
            margin: 36px auto;
            padding: 10px 16px;
            border-radius: 34px;
            width: fit-content;
            height: 100%;
            display: flex;
            align-items: center;
            cursor: pointer;
            transition: all 0.2s linear;
            border: 1px solid var(--faint-color);
            background-color: var(--faint-color);
        
            &:hover {
                box-shadow: var(--second-box-shadow)
            }
        
            svg {
                width: 24px;
                height: 24px;
                fill: var(--main-color);
                margin-right: 8px;
            }
        
            span {
                color: var(--main-color);
                font-weight: 500;
                font-size: 18px;
                letter-spacing: 0.74px;
            }
        }
    }

    .sort-area_sort {
        margin-bottom: 36px;
    }

    .sort-area_actions {
        margin-bottom: 36px;
    }

    .sort-area_related {
        margin-top: -18px;
    }
    
}