.category-list {
    .category-list_header {
        margin-bottom: 16px;

        .category-list_header-button {
            width: 100%;
            display: flex;
            align-items: center;
            padding: 10px 20px 10px 12px;
            background-color: var(--faint-color);
            border-radius: 50px;
            color: var(--main-color);
            transition: all 0.3s linear;
           

            svg {
                width: 24px;
                height: 24px;

                margin-right: 8px;
                fill: var(--main-color);
            }

            span {
                font-size: 24px;
                font-weight: 500;
                line-height: 36px;
                letter-spacing: 0.45px;
                color: var(--main-color);
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }

            &:hover {
                cursor: pointer;
                box-shadow: var(--second-box-shadow);
            }
        }
       


    }

    .category-list_body {
        margin-left: 16px;

        .category-list_item {
            display: flex;
            align-items: center;
            height: 40px;
            padding: 8px 12px; 
            max-width: 100%;
            width: fit-content;
           

            span {
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                max-width: 100%;
            }

            &:hover {
                cursor: pointer;
                box-shadow: var(--second-shadow-theme);
            }
        }
    }
}