.util-button {
    display: flex;
    align-items: center;
    width: fit-content;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 20px;
    border-radius: 4px;
    background-color: transparent;
    cursor: pointer;
    white-space: nowrap;
    transition: all 0.3s linear;

    &:hover {
        box-shadow: var(--second-box-shadow);
    }

    &:active {
        opacity: 0.8;
    }

    .util-button_icon {
        width: 20px;
        height: 20px;
        margin-right: 8px;
        color: var(--main-color);
    }

    .util-button_icon svg {
        fill: var(--main-color);
    }

    .util-button_content {
        color: var(--main-color);
    }
}