.button-seemore {
    padding: 8px 12px;
    font-size: 14px;
    color: var(--main-color);
    background-color: var(--faint-color);
    font-weight: normal;
    white-space: nowrap;
    cursor: pointer;
    border-radius: 4px;
    letter-spacing: 0.75px;
    min-width: 120px;
    max-width: 174px;
    width: fit-content;
    position: relative;

    &:hover {
        box-shadow: var(--second-box-shadow);

        .button-seemore_tooltip {
            display: block
        }
    }
    

    .button-seemore_content {
        text-overflow: ellipsis;
        overflow: hidden;
        text-align: center;
    }

    .button-seemore_tooltip {
        display: none;
        position: absolute;
        top: calc(100% + 12px);
        left: 50%;
        transform: translateX(-50%);
        padding: 8px;
        font-size: 12px;
        font-weight: normal;
        color: var(--text-white);
        background-color: var(--text-color-faint);
        border-radius: 4px;
        z-index: 999;
    }

    .button-seemore_tooltip.top {
        top: initial;
        bottom: calc(100% + 6px);
    }

    .button-seemore_tooltip.bottom {
        top: calc(100% + 12px);
    }

    .button-seemore_tooltip.center {
        left: 50%;
        transform: translateX(-50%);
    }

    .button-seemore_tooltip.left {
        left: 0;
        transform: translateX(0);
    }

    .button-seemore_tooltip.right {
        right: 0;
        transform: translateX(0);
    }
}