.search {
    height: 100%;
    min-width: 32px;
    width: 100%;
    display: flex;
    align-items: center;
    padding: 0 8px;
    margin-left: auto;
    background-color: #f0f0f1;
    border-radius: 50px;

    &.active {
        background-color: var(--faint-color) !important;
    }

    &.search--full {
        padding: 0 8px !important;
        width: 100% !important;
    }

    &:hover {
        .search_icon {
            color: var(--main-color);
        }
    }

    .search_icon {
        width: 32px;
        height: 32px;
        font-size: 22px;
        cursor: pointer;
        color: var(--text-color-light);
        display: flex;
        align-items: center;
        justify-content: center;

        &:hover {
            color: var(--main-color);
        }
    }

    .search_input {
        height: 100%;
        width: 100%;
        background-color: transparent;
        border: none;
        outline: none;
        color: var(--text-color-light);
        font-size: 18px;

        &::placeholder {
            color: var(--text-color-light);
        }
    }
}

@media (max-width: 1024px) {  
    .search {
        padding: 0 6px;
        width: 32px;
        transition: all .5s linear;

        .search_input {
            font-size: 16px;
        }
    }
}