.category-area {
    height: 100%;
    width: 100%;
    overflow-y: scroll;

    &::-webkit-scrollbar {
        width: 0;
    }

    .category-area_header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 4px;
        margin-bottom: 24px;

        h3 {
            font-size: 34px;
            font-weight: 400;
        }
    }

    .category-area_body {
        display: flex;
        flex-wrap: wrap;


        .category-area_body-item {
            width: 33.333%;
            padding: 0 12px;
            margin-bottom: 24px;
        }
    }
    
}


@media (max-width: 876px) { 
    .category-area {

        .category-area_header {

            h3 {
                margin-bottom: 12px;
            }
            flex-direction: column;
        }
    
        .category-area_body {

            .category-area_body-item {
                width: 50%;
            }
        }
    }
}

@media (max-width: 647px) { 
    .category-area {
    
        .category-area_body {

            .category-area_body-item {
                width: 100%;
            }
        }
    }
}


@media (max-width: 474px) { 
    .category-area {

        .category-area_header {
            margin-bottom: 16px;

            .category-area_header-btn {
                display: none;
            }
        }
    
        .category-area_body {

            .category-area_body-item {
                width: 100%;
            }
        }
    }
}