.button-category {

    padding: 10px 20px 8px 12px;
    border-radius: 50px;
    border: 1px solid var(--second-color);
    width: fit-content;
    height: 36px;
    box-sizing: initial;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: all 0.2s linear;

    &:hover {
        background-color: var(--faint-color);
    }

    svg {
        width: 24px;
        height: 24px;
        fill: var(--main-color);
        margin-right: 8px;
    }

    span {
        color: var(--main-color);
        font-weight: 400;
        font-size: 24px;
        letter-spacing: 0.75px;
    }

    &.background {
        border: 1px solid var(--faint-color);
        background-color: var(--faint-color);
        
        &:hover {
            box-shadow: var(--second-box-shadow)
        }
    }
}