.combo-box {
    position: relative;
    width: 204px;
    height: 36.85px;
    border-radius: 4px;
     background-color: var(--main-bg);

    .combo-box_parent {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 100%;
        width: 100%;
        padding: 0 12px;
        border-radius: 4px;
        border: 1px solid var(--border-color);
        cursor: pointer;
        transition: all 0.24s linear;

        &:hover {
            border-color: var(--main-color);

            &.label {
                .combo-box_parent-label {
                    color: var(--main-color);
                }
            }
        }

        .combo-box_parent-label {
            display: none;
        }

        &.label {
            .combo-box_parent-label {
                display: block;
                position: absolute;
                top: 0;
                transform: translateY(-50%);
                background: var(--main-bg);
                text-transform: uppercase;
                font-size: 10px;
                color: var(--text-color-light);
                font-weight: 500;
                padding: 0 4px;
                line-height: 12px;
            }
        }

        &.selected {
            border-color: var(--text-color);

            .combo-box_parent-placeholder {
                color: var(--text-color);
            }
        }

        .combo-box_parent-placeholder {
            color: var(--border-color);
            font-size: 16px;
            font-weight: 400;
        }

        .combo-box_parent-icon svg {
            width: 24px;
            height: 24px;
            fill: var(--text-color-light);
        }
    }

    .combo-box_children {
        display: none;
        position: absolute;
        top: -4px;
        left: -4px;
        font-size: 16px;
        max-height: 256px;
        min-width: calc(100% + 8px);
         background-color: var(--main-bg);
        border-radius: 12px;
        box-shadow: var(--shadow-theme-big);
        z-index: 5;
        opacity: 0;
        transform-origin: 50% 24px 0px;
        transform: scaleY(0);
        transition: all 0.3s linear;

        
        .combo-box_item {
            height: 48px;
            padding: 0 12px;
            display: flex;
            align-items: center;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            user-select: none;
            line-height: 0.44px;
            font-size: 16px;

            &:first-child {
                border-top-left-radius: 12px;
                border-top-right-radius: 12px;
            }

            &:last-child {
                border-bottom-left-radius: 12px;
                border-bottom-right-radius: 12px;
            }

            &:hover {
                cursor: pointer;
                color: #fff;
                background-image: var(--combobox-bg);
            }

            &.active {
                cursor: default;
                color: #fff;
                background-image: var(--combobox-bg);
            }
        }
    }

    &.open {
       
        .combo-box_children {
            display: block;
            opacity: 1;
            transform: scaleY(1);
        }
    }
}

@media (max-width: 640px) { 
    .combo-box {
        width: 100% !important;
    }
}