.theme-area {
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    gap: 8px;
    padding: 8px 12px;
    border-radius: 8px;
    background-color: var(--main-bg);
    box-shadow: var(--second-shadow-theme);
    width: fit-content;

    .theme-area_item {
        width: 24px;
        height: 24px;
        border-radius: 50%;
        cursor: pointer;
        box-shadow: var(--shadow-theme);
    }
}