.related-wrapper {
    display: flex;
    align-items: center;
    gap: 8px;
    height: auto;
    min-height: 48px;
    flex-wrap: wrap;
    padding:  8px 0;
    margin-bottom: 16px;
    overflow-x: scroll;
    user-select: none;
    z-index: 1;

    .related-wrapper-title {
        display: none;
        color: var(--text-color);
        font-size: 14px;
        font-weight: 600;
    }

    &.related-title {
        .related-wrapper-title {
            display: block;
        }
    }

    &::-webkit-scrollbar {
        height: 0;
        width: 0;
    }
}