.promotion {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    background-color: #ebf2ff;
    padding: 24px 16px;
    border: 1px solid transparent;
    transition: box-shadow .28s cubic-bezier(.4,0,.2,1), background-color .28s cubic-bezier(.4,0,.2,1);
    cursor: pointer;

    &:hover {
        border: 1px solid #9dc2ff;
    }

    &.warning {
        background-color: #fdedf2;

        &:hover {
            border: 1px solid #f6a8c2;
        }
    }

    .promotion_img {
        width: 80px;
        height: 80px;
    }

    .promotion_title {
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 32px;
        margin-bottom: 4px;
        text-align: center;
    }

    .promotion_description {
        color: #787885;
        font-weight: 500;
        font-size: 14px;
        line-height: 22px;
        letter-spacing: .1px;
        text-align: center;
    }
}