.card {
    width: 100%;
    height: 100%;
    padding: 16px 16px 12px;
    background-color: var(--main-bg);
    transition: box-shadow .28s cubic-bezier(.4,0,.2,1),background-color .28s cubic-bezier(.4,0,.2,1);
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    &:hover {
        box-shadow: var(--second-box-shadow);
    }

    .card_info {
        margin-bottom: 10px;
       
        img {
            width: 100%;
            height: auto;

        }

        .card_info-name {
            font-size: 16px;
            line-height: 24px;
            font-weight: normal;
            letter-spacing: 0.45px;
            margin-bottom: 8px;
            color: var(--text-color);
        }

        .card_info-price {
            font-size: 24px;
            line-height:36px;
            margin-bottom: 8px;
            font-weight: bold;
        }

        .card_info-description {
            color: var(--text-color-light);
            font-weight: 400;
            letter-spacing: .44px;
            line-height: 24px;
            font-size: 14px;
        }
    }

    .card_utils {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .card_utils-rating {
            display: flex;
            flex-wrap: nowrap;
            gap: 2px;

            svg {
                width: 14px;
                height: 14px;
                fill: #ffb41f;
            }

            span {
                margin-left: 8px;
                font-size: 16px;
                font-weight: 500;
            }
        }

        .card_utils-button {
            display: flex;
            align-items: center;
            padding: 0 8px;
            border-radius: 4px;
            border: 1px solid var(--second-color);
            margin-left: 8px;

            &:hover {
                cursor: pointer;
                background-color: var(--faint-color);
                transition: background-color .25s cubic-bezier(.4,0,.2,1) 0ms,box-shadow .25s cubic-bezier(.4,0,.2,1) 0ms,border .25s cubic-bezier(.4,0,.2,1) 0ms;
            }

            .card_utils-button--icon {
                width: 16px;
                height: 16px;
                fill: var(--main-color);
                margin-right: 8px;;
            }

            .card_utils-button--text {
                font-size: 16px;
                font-weight: normal;
                color: var(--main-color);
                line-height: 32px;
                white-space: nowrap;
            }
        }
    }
}